body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.content {
    min-height: calc(100vh - 70px - 64px);
    padding: 1.5rem 0;
}

.goods {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1rem;

}

.card {
    display: flex;
    flex-direction: column;

}

.card-content {
    flex-grow: 1;
}

.cart {
    position: fixed;
    bottom: 2rem;
    right: 2rem;

    cursor: pointer;
    z-index: 5;
    padding: 0.6rem;
}

@media (min-width: 767px) {
    .cart {
        top: 6.2rem;
        bottom: unset;
    }
}

.cart-quantity {
}


.basket-list {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-color: #133134;
    width: 50rem;
    max-width: 95%;
    font-size: calc(5px + 2vmin);

    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.5);
    animation: show 500ms ease-in-out;
}

.close-basket {
    position: absolute;
    top: 0.5rem;
    right: 1rem;
    color: #f1d3cb;
    cursor: pointer;

}

.item {
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /*text-align: left;*/
}

.checkout {
    margin-left: 20rem
}

.delete-item {
    cursor: pointer;
    color: black;
}

@keyframes show {
    from {
        top: 70%;
        opacity: 0;
    }
    to {
        top: 50%;
        opacity: 1;
    }

}

.basket-quantity {
    cursor: pointer;
    vertical-align: middle;
    color: #e65100;
}


.basket-text {
    width: 250px;
}


.text-color {
    color: lavender;
}

#toast-container {
    top: 3rem !important;
    animation: fade-in 250ms ease-in-out;
}

@keyframes fade-in {
    from {
        opacity: 0;
        transform: translateY(2rem);
    }
    to {
        opacity: 1;
        transform: translateY(0rem);

    }
}